import Vue              from 'vue';
import App              from './App.vue';
import vuetify          from './plugins/vuetify';
import VueCodeHighlight from 'vue-code-highlight';

Vue.config.productionTip = false;
Vue.use(VueCodeHighlight);
new Vue({
	vuetify,
	render: h => h(App)
}).$mount('#app');
